.overphone {
    margin-top: 60px;

&__title {
    font-size: 33px;
    margin-bottom: 30px;

@media all and (max-width: 1200px) {
    text-align: center;
}
}

&__subtitle {
    color: rgba($Black, 0.65);
    font-size: 18px;
    margin-bottom: 30px;

    @media all and (max-width: 1200px) {
        text-align: center;
    }
}

&__button {
    display: flex;
    justify-content: space-around;

    @media all and (max-width: 1200px) {
        width: 100% !important;
    }
    
    & a {
        padding: 5px;
        height: 45px;
        width: 200px;

        @media all and (max-width: 1200px) {
            width: 100% !important;
        }
    }
} // .overphone__button
} // .overphone

